@mixin menu-split-animation($RL, $nth, $width, $delayIn, $delayOut) {

	.split#{$RL} li:nth-of-type(#{$nth}) {
		margin-left: #{$width / 2}px;
		transition-delay: #{$delayIn}s;
	}
	.split#{$RL} li.animate:nth-of-type(#{$nth}) {
		margin-left: 0;
		transition-delay: #{$delayOut}s;
	}
}

